  }



  /* -------MainOpt */

  .MainOpt {
    height: 45em;
    grid-template-columns: 2fr 1fr;
    grid-template-Rows: 1fr 3fr;
    grid-template-areas:
      "leftTop RightTop"
      "LeftBot RightBot"
    ;
  }

  .MainOpt .MainOptParag {
    /* background-color: aquamarine; */
    justify-self: center;
    right: 10%;
    width: 90%;
    position: relative;
  }

  .MainOpt .Left {
    grid-area: LeftBot;
  }

  .MainOpt .Center {
    grid-area: leftTop;
  }

  .MainOpt .Right {
    grid-area: RightTop;
  }



  /* -------MainGeraphic */

  .MainGeraphic {
    height: 45em;
    grid-template-columns: 1fr 4fr;
    grid-template-Rows: 1fr 1fr;
    grid-template-areas:
      "LeftTop RightTop"
      ". RightBot"
    ;
  }

  .MainGeraphic .Center {
    grid-area: RightTop;
  }

  .MainGeraphic .Right {
    grid-area: RightBot;
  }

  /* -------MainAdds */
  .MainAdds {
    height: 45em;
    grid-template-columns: 3fr 1fr;
    grid-template-Rows: 1fr 3fr;
    grid-template-areas:
      "leftTop RightTop"
