    ;
  }


  .MainAdds .Left img {
    width: 80%;
    height: 80%;
  }

  .MainAdds .Left {
    grid-area: LeftTop;
  }

  .MainAdds .Center {
    grid-area: leftTop;
  }

  .MainAdds .Right {
    grid-area: RightTop;
  }
